	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked.co.uk","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/thought-leadership/index.md absPath of file >>> MarkdownRemark","html":"<p>Some concepts transcend time and technology specifics. To make it easier to get yourself up to date with these ideas, I'm going to list my favourite articles, links and publications on a single page.</p>\n<h2>Development Management</h2>\n<h3>Article: <a href=\"https://chrismm.com/blog/how-terrible-code-gets-written-by-perfectly-sane-people/\">How terrible code gets written by perfectly sane people.</a></h3>\n<p>Nobody sets out to write poor code, or come up with a poor solution to a problem, but an emphasis on certain specific aspects of software development can have a toxic effect on the outcome. Awareness of the potential pitfalls makes it easier to recognise these issues and avoid them altogether, and these pitfalls and their symptoms is what this article discusses.</p>","frontmatter":{"title":"Thought Leadership","date":"September 30, 2017","tags":null,"url":null}}},"pathContext":{"prev":{"frontmatter":{"path":"/blog/text-layout-framework-for-flash-player-10/","tags":["flash"],"title":"Text Layout Framework for Flash Player 10","image":null,"date":"2009-01-05T12:42:27.000Z"}},"next":{"frontmatter":{"path":"/blog/there-be-pixels/","tags":["bitmap","bitmapdata","pixels"],"title":"There Be Pixels!","image":null,"date":"2009-01-12T18:06:47.000Z"}}}}

